import { ClockIcon, ShareIcon } from '@heroicons/react/24/outline'
import { format } from 'date-fns'
import Countdown from 'react-countdown'

import {
  DATE_LOCALE,
  ENABLE_ARCHIVED_GAMES,
  ENABLE_MIGRATE_STATS,
} from '../../constants/settings'
import {
  ARCHIVE_GAMEDATE_TEXT,
  NEW_WORD_TEXT,
  SHARE_TEXT,
  STATISTICS_TITLE,
  WIN_MESSAGES,
} from '../../constants/strings'
import { Cipher } from '../../lib/cipher'
import { GameStats } from '../../lib/localStorage'
import {
  Solution,
  solution,
  solutionGameDate,
  tomorrow,
} from '../../lib/quotes'
import { shareStatus } from '../../lib/share'
import { MigrationIntro } from '../stats/MigrationIntro'
import { StatBar } from '../stats/StatBar'
import { BaseModal } from './BaseModal'

type Props = {
  cipher: Cipher
  encryptedQuote: Solution
  isOpen: boolean
  handleClose: () => void
  gameStats: GameStats
  isLatestGame: boolean
  isGameLost: boolean
  isGameWon: boolean
  handleShareToClipboard: () => void
  handleShareFailure: () => void
  handleMigrateStatsButton: () => void
  isHardMode: boolean
  isDarkMode: boolean
  isHighContrastMode: boolean
}
const queryParams = new URLSearchParams(window.location.search)

export const StatsModal = ({
  cipher,
  encryptedQuote,
  isOpen,
  handleClose,
  gameStats,
  isLatestGame,
  isGameLost,
  isGameWon,
  handleShareToClipboard,
  handleShareFailure,
  handleMigrateStatsButton,
  isDarkMode,
  isHighContrastMode,
}: Props) => {
  const label = solution.author ? 'The quote was' : 'The message was'
  const shouldShowStats =
    isGameWon && !queryParams.get('code') && !queryParams.get('d')

  const winMessage =
    WIN_MESSAGES[Math.floor(Math.random() * WIN_MESSAGES.length)]
  const gameWonOrLostMessage = isGameWon ? winMessage : ''
  return (
    <BaseModal
      title={shouldShowStats ? STATISTICS_TITLE : gameWonOrLostMessage}
      isOpen={isOpen}
      handleClose={handleClose}
    >
      {isGameLost || isGameWon ? (
        <div className="mb-6 mt-5 columns-1 items-center items-stretch justify-center text-center dark:text-white sm:mt-6">
          <span>{label}: </span>
          <br />
          <span aria-label={label}>{solution.quote}</span>
          <br />
          {solution.author}
        </div>
      ) : null}
      {shouldShowStats ? <StatBar gameStats={gameStats} /> : null}
      <div className="mt-5 columns-2 items-center items-stretch justify-center text-center dark:text-white sm:mt-6">
        <div className="inline-block w-full text-left">
          {!ENABLE_ARCHIVED_GAMES || isLatestGame ? (
            <div>
              <h5>{NEW_WORD_TEXT}</h5>
              <Countdown
                className="text-lg font-medium text-gray-900 dark:text-gray-100"
                date={tomorrow}
                daysInHours={true}
              />
            </div>
          ) : null}
          {queryParams.get('d') ? (
            <div className="mt-2 inline-flex">
              <ClockIcon className="mr-1 mt-1 mt-2 h-5 w-5 stroke-black dark:stroke-white" />
              <div className="ml-1 mt-1 text-center text-sm sm:text-base">
                <strong>{ARCHIVE_GAMEDATE_TEXT}:</strong>
                <br />
                {format(solutionGameDate, 'd MMMM yyyy', {
                  locale: DATE_LOCALE,
                })}
              </div>
            </div>
          ) : null}
        </div>
        <div>
          <button
            type="button"
            className="mt-2 inline-flex w-full items-center justify-center rounded-md border border-transparent bg-federal-blue-600 px-4 py-2 text-center text-sm font-medium text-white shadow-sm hover:bg-federal-blue-700 focus:outline-none focus:ring-2 focus:ring-federal-blue-500 focus:ring-offset-2 sm:text-base"
            onClick={() => {
              shareStatus({
                cipher,
                encryptedQuote,
                handleShareFailure,
                handleShareToClipboard,
                isDarkMode,
                isGameWon,
                isHighContrastMode,
              })
            }}
          >
            <ShareIcon className="mr-2 h-6 w-6 cursor-pointer dark:stroke-white" />
            {SHARE_TEXT}
          </button>
        </div>
      </div>
      {ENABLE_MIGRATE_STATS && (
        <div>
          <hr className="-mb-4 mt-4 border-gray-500" />
          <MigrationIntro handleMigrateStatsButton={handleMigrateStatsButton} />
        </div>
      )}
    </BaseModal>
  )
}
