import './App.css'

import { format } from 'date-fns'
import { useCallback, useEffect, useState } from 'react'
import Div100vh from 'react-div-100vh'

import { AlertContainer } from './components/alerts/AlertContainer'
import { Alphabet } from './components/alphabet/Alphabet'
import { Cryptogram } from './components/cryptogram/Cryptogram'
import { DatePickerModal } from './components/modals/DatePickerModal'
import { HeartModal } from './components/modals/HeartModal'
import { InfoModal } from './components/modals/InfoModal'
import { MigrateStatsModal } from './components/modals/MigrateStatsModal'
import { SettingsModal } from './components/modals/SettingsModal'
import { ShareMessageModal } from './components/modals/ShareMessageModal'
import { StatsModal } from './components/modals/StatsModal'
import { TouchScreenInfoModal } from './components/modals/TouchScreenInfoModal'
import { Navbar } from './components/navbar/Navbar'
import {
  CELEBRATE_TIME_MS,
  DATE_LOCALE,
  DISCOURAGE_INAPP_BROWSERS,
  LONG_ALERT_TIME_MS,
  MAX_HINTS,
  MAX_INCORRECT_GUESSES as MAX_INCORRECT_GUESSES_SETTINGS,
  REVEAL_TIME_MS,
  WELCOME_INFO_MODAL_MS,
} from './constants/settings'
import {
  BOOK_COVER_THUMBNAIL,
  DISCOURAGE_INAPP_BROWSER_TEXT,
  GAME_COPIED_MESSAGE,
  GAME_TITLE,
  HARD_MODE_ALERT_MESSAGE,
  LINK_COPIED_MESSAGE,
  SHARE_FAILURE_TEXT,
  SHARE_LINK_FAILURE_TEXT,
  WARNING_REMAINING_GUESSES,
  WIN_MESSAGES,
  YOU_HAVE_ALREADY_GUESSED_MESSAGE,
} from './constants/strings'
import { useAlert } from './context/AlertContext'
import { isInAppBrowser } from './lib/browser'
import {
  ALPHABET,
  encodePhrase,
  generateCryptogramHint,
  newCipher,
} from './lib/cipher'
import {
  Guess,
  getStoredIsHighContrastMode,
  loadGameStateFromLocalStorage,
  loadHasSeenInfoModalFromLocalStorage,
  loadHasUsedHardModeFromLocalStorage,
  loadHasUsedHintFromLocalStorage,
  loadIsTouchScreenUserConfused,
  saveGameStateToLocalStorage,
  saveHasSeenInfoModalToLocalStorage,
  saveHasUsedHardModeToLocalStorage,
  saveHasUsedHintToLocalStorage,
  saveIsTouchScreenUserConfused,
  setStoredIsHighContrastMode,
} from './lib/localStorage'
import {
  getGameDate,
  getIsLatestGame,
  localeAwareUpperCase,
  setGameDate,
  solution,
  solutionGameDate,
  solutionIndex,
  solutionName,
} from './lib/quotes'
import { addStatsForCompletedGame, loadStats } from './lib/stats'
import { CharStatus } from './lib/statuses'

const isAndroid =
  !window.location.search.includes('android=beta') &&
  /Android/i.test(navigator.userAgent)

const isIPad =
  !window.location.search.includes('ipad=beta') &&
  /iPad/i.test(navigator.userAgent)

// const betaMode = window.location.search.includes('beta')
const cipher = newCipher(solutionIndex)
const isTouchScreen = 'ontouchstart' in window || navigator.maxTouchPoints > 0
let isFirstGame = true
const debug = (...args: any[]) => {
  // console.log(args)
}

const encryptedQuote = {
  author: encodePhrase({
    cipher,
    phrase: solution.author?.toLocaleUpperCase() || '',
  }),
  quote: encodePhrase({
    cipher,
    phrase: solution.quote.toLocaleUpperCase(),
  }),
}
const encryptedLetters: string[] = []
const solutionLetters: string[] = []
const encryptedString: string = `${encryptedQuote.quote}${
  encryptedQuote.author ? encryptedQuote.author : ''
}`
encryptedString.split('').forEach((letter) => {
  if (cipher[letter] && !encryptedLetters.includes(letter)) {
    encryptedLetters.push(letter)
  }
})
const solutionString = `${solution.quote}${solution.author}`
solutionString
  .toLocaleUpperCase()
  .split('')
  .forEach((letter) => {
    if (ALPHABET.includes(letter) && !solutionLetters.includes(letter)) {
      solutionLetters.push(letter)
    }
  })
let userHasGuessedALetter = false
let userHasIncorrectlyGuessedALetter = false
let gameWasAlreadyCompleted = false

function App() {
  const hasUsedAHint = loadHasUsedHintFromLocalStorage()
  const hasUsedHardMode = loadHasUsedHardModeFromLocalStorage()
  const isLatestGame = getIsLatestGame()
  const gameDate = getGameDate()
  const formattedGameDate = format(gameDate, 'd MMMM yyyy', {
    locale: DATE_LOCALE,
  })
  // We decided to always default to dark mode, even for users who computer is not in dark mode
  // const prefersDarkMode = window.matchMedia(
  //   '(prefers-color-scheme: dark)'
  // ).matches

  const { showError: showErrorAlert, showSuccess: showSuccessAlert } =
    useAlert()
  const [currentCipher, setCurrentCipher] = useState(cipher)
  const [isGameWon, setIsGameWon] = useState(false)
  const [celebrateGameIsWon, setCelebrateGameIsWon] = useState(false)
  const [hintCount, setHintCount] = useState(0)
  const [isHeartModalOpen, setIsHeartModalOpen] = useState(false)
  const [isInfoModalOpen, setIsInfoModalOpen] = useState(false)
  const [isTouchScreenInfoModalOpen, setIsTouchScreenInfoModalOpen] =
    useState(false)
  const [isTouchScreenUserConfused, setIsTouchScreenUserConfused] = useState(
    loadIsTouchScreenUserConfused()
  )
  const [isSendMessageModalOpen, setIsSendMessageModalOpen] = useState(false)
  const [isStatsModalOpen, setIsStatsModalOpen] = useState(false)

  const [isDatePickerModalOpen, setIsDatePickerModalOpen] = useState(false)
  const [isMigrateStatsModalOpen, setIsMigrateStatsModalOpen] = useState(false)
  const [isSettingsModalOpen, setIsSettingsModalOpen] = useState(false)
  const [isGameLost, setIsGameLost] = useState(false)
  const [isPrintLayout, setIsPrintLayout] = useState(false)
  const [isDarkMode, setIsDarkMode] = useState(
    localStorage.getItem('theme')
      ? localStorage.getItem('theme') === 'dark'
      : true
  )
  const [isHighContrastMode, setIsHighContrastMode] = useState(
    getStoredIsHighContrastMode()
  )
  const [currentLetter, setCurrentLetter] = useState('')
  const [incorrectGuesses, setIncorrectGuesses] = useState<Guess[]>([])
  const [isHardMode, setIsHardMode] = useState(
    localStorage.getItem('gameMode')
      ? localStorage.getItem('gameMode') === 'hard'
      : false
  )
  if (isHardMode) {
    saveHasUsedHardModeToLocalStorage()
  }
  const [gameTitle, setGameTitle] = useState(
    `${GAME_TITLE} - ${window.location.search.includes('code=') ? 'encrypted message' : formattedGameDate}`
  )
  const MAX_INCORRECT_GUESSES = isHardMode
    ? Math.max(MAX_INCORRECT_GUESSES_SETTINGS, ALPHABET.length)
    : MAX_INCORRECT_GUESSES_SETTINGS

  const [guesses, setGuesses] = useState<Guess[]>(() => {
    const loaded = loadGameStateFromLocalStorage()
    if (loaded?.solution.quote !== solution.quote) {
      return []
    }
    const gameWasWon = loaded?.gameWasWon
    if (gameWasWon) {
      gameWasAlreadyCompleted = true
      setIsGameWon(true)
      setTimeout(() => {
        setCelebrateGameIsWon(true)
        setTimeout(() => {
          setCelebrateGameIsWon(false)
        }, CELEBRATE_TIME_MS)
      }, CELEBRATE_TIME_MS / 2)
    }
    if (
      !isHardMode &&
      loaded.guesses.length === MAX_INCORRECT_GUESSES &&
      !gameWasWon
    ) {
      gameWasAlreadyCompleted = true
      setIsGameLost(true)
    }
    setTimeout(() => {
      // restore incorrect guesses
      loaded.incorrectGuesses.forEach(({ input, label }) => {
        onChar(input, label as string)
      })
      // render all the (correct) guesses
      guesses
        .filter(({ status }) => status === 'correct')
        .forEach(({ input }) => {
          Object.keys(cipher).forEach((key) => {
            if (input === cipher[key].decrypted) {
              onChar(cipher[key].decrypted, key)
            } else {
              debug(
                'this was an incorrect guess, we arent sure where to put it ',
                input
              )
            }
          })
        })
      setIncorrectGuesses(loaded.incorrectGuesses)
    }, 100)
    return loaded.guesses
  })

  const [stats, setStats] = useState(() => loadStats())

  useEffect(() => {
    // if no game state on load,
    // show the user the how-to info modal
    if (!loadHasSeenInfoModalFromLocalStorage()) {
      if (isFirstGame) {
        isFirstGame = false
        window.userInteractionEvent('event', 'sign_up', {
          method: 'Anonymous',
        })
      }
      setTimeout(() => {
        setIsInfoModalOpen(true)
      }, WELCOME_INFO_MODAL_MS)
    }
  }, [])

  useEffect(() => {
    DISCOURAGE_INAPP_BROWSERS &&
      isInAppBrowser() &&
      showErrorAlert(DISCOURAGE_INAPP_BROWSER_TEXT, {
        persist: false,
        durationMs: 7000,
      })
  }, [showErrorAlert])

  useEffect(() => {
    if (isDarkMode) {
      document.documentElement.classList.add('dark')
    } else {
      document.documentElement.classList.remove('dark')
    }

    if (isHighContrastMode) {
      document.documentElement.classList.add('high-contrast')
    } else {
      document.documentElement.classList.remove('high-contrast')
    }
  }, [isDarkMode, isHighContrastMode])

  const handleDarkMode = (isDark: boolean) => {
    setIsDarkMode(isDark)
    const colorMode = isDark ? 'dark' : 'light'
    localStorage.setItem('theme', colorMode)
    window.userInteractionEvent('event', 'unlock_achievement', {
      achievement_id: `choose_${colorMode}_mode`,
    })
  }

  const handleHardMode = (isHard: boolean) => {
    if (guesses.length === 0 || localStorage.getItem('gameMode') === 'hard') {
      setIsHardMode(isHard)
      const gameMode = isHard ? 'hard' : 'normal'
      localStorage.setItem('gameMode', gameMode)
      window.userInteractionEvent('event', 'unlock_achievement', {
        achievement_id: `choose_${gameMode}_mode`,
      })
    } else {
      showErrorAlert(HARD_MODE_ALERT_MESSAGE)
    }
  }

  const handleHighContrastMode = (isHighContrast: boolean) => {
    const colorMode = isHighContrast ? 'high_contrast' : 'normal_contrast'
    setIsHighContrastMode(isHighContrast)
    setStoredIsHighContrastMode(isHighContrast)
    window.userInteractionEvent('event', 'unlock_achievement', {
      achievement_id: `choose_${colorMode}`,
    })
  }

  useEffect(() => {
    saveGameStateToLocalStorage({
      guesses,
      gameWasWon: isGameWon,
      incorrectGuesses,
      solution,
    })
  }, [guesses, incorrectGuesses, isGameWon])

  useEffect(() => {
    if (
      !isTouchScreenUserConfused.hasSeenTheModal &&
      !isTouchScreenUserConfused.hasSucceededToInput &&
      isTouchScreenUserConfused.placeholderClicks.length >= 2
    ) {
      setIsTouchScreenInfoModalOpen(true)
    }
  }, [isTouchScreenUserConfused])

  const [hintTimeout, setHintTimeout] = useState<NodeJS.Timeout | null>(null)
  // If the user solved the puzzle in under 60 seconds, prompt them to try hard mode
  const [tryHardModeTimeout, setTryHardModeTimeout] =
    useState<NodeJS.Timeout | null>(null)
  const [shouldTryHardMode, setShouldTryHardMode] = useState(
    !isHardMode && !hasUsedHardMode && !isGameWon && !gameWasAlreadyCompleted
  )

  useEffect(() => {
    if (isGameWon) {
      const winMessage =
        WIN_MESSAGES[Math.floor(Math.random() * WIN_MESSAGES.length)]
      const delayMs = REVEAL_TIME_MS

      if (shouldTryHardMode && !gameWasAlreadyCompleted && !isHardMode) {
        window.userInteractionEvent('event', 'unlock_achievement', {
          achievement_id: 'should_try_hard_mode',
        })
        showSuccessAlert(
          'Want an extra challenge? You can try hard mode next time by clicking on the gear icon.',
          {
            persist: false,
            delayMs: REVEAL_TIME_MS,
            durationMs: REVEAL_TIME_MS * 20,
            onClose: () => setIsStatsModalOpen(true),
          }
        )
      } else {
        showSuccessAlert(winMessage, {
          delayMs,
          onClose: () => setIsStatsModalOpen(true),
        })
      }
    }

    if (isGameLost) {
      setTimeout(() => {
        setIsStatsModalOpen(true)
      }, REVEAL_TIME_MS)
    }
  }, [
    isGameWon,
    isGameLost,
    isHardMode,
    showSuccessAlert,
    shouldTryHardMode,
    setIsStatsModalOpen,
  ])

  // If the user has not had a correct guess in the first 10 seconds, prompt them to use a hint
  useEffect(() => {
    if (
      true ||
      hasUsedAHint ||
      gameWasAlreadyCompleted ||
      isGameWon ||
      isGameLost
    ) {
      return
    }
    setHintTimeout(
      setTimeout(() => {
        window.userInteractionEvent('event', 'unlock_achievement', {
          achievement_id: 'should_try_a_hint',
        })
        showSuccessAlert(
          "If you aren't sure where to start, you can use a hint by clicking the puzzle icon.",
          {
            persist: false,
            durationMs: REVEAL_TIME_MS * 20,
          }
        )
      }, 10000)
    )
  }, [
    hasUsedAHint,
    isGameLost,
    isGameWon,
    showSuccessAlert,
    tryHardModeTimeout,
  ])

  useEffect(() => {
    setTryHardModeTimeout(
      setTimeout(() => {
        setShouldTryHardMode(false)
      }, 1000 * 60)
    )
  }, [setShouldTryHardMode])

  const onChar = useCallback(
    (input: string, ariaLabel: string) => {
      if (isGameWon || isGameLost) {
        return
      }
      debug('input', input, 'ariaLabel', ariaLabel)
      const label = ariaLabel || currentLetter
      if (
        label &&
        input &&
        currentCipher[label] &&
        input !== currentCipher[label].guesses[0]
      ) {
        const updatedCipher = { ...currentCipher }

        // let alreadyGuessed = false
        Object.keys(updatedCipher).forEach((key) => {
          if (label !== key && updatedCipher[key].guesses[0] === input) {
            // alreadyGuessed = true
            showErrorAlert(YOU_HAVE_ALREADY_GUESSED_MESSAGE(input, key), {
              persist: false,
            })
          }
        })

        updatedCipher[label].guesses = [input, ...updatedCipher[label].guesses]
        let status: CharStatus | undefined = undefined
        if (input === updatedCipher[label].decrypted) {
          status = 'correct'
          updatedCipher[label].status = status
          if (!userHasGuessedALetter) {
            userHasGuessedALetter = true
            window.userInteractionEvent('event', 'unlock_achievement', {
              achievement_id: 'make_correct_guess',
            })
          }
          hintTimeout && clearTimeout(hintTimeout)
        } else if (solutionLetters.includes(input)) {
          status = 'present'
          updatedCipher[label].status = status
          updatedCipher[label].firstIncorrectGuess = status
        } else {
          status = 'absent'
          updatedCipher[label].status = status
          updatedCipher[label].firstIncorrectGuess = status
        }
        setGuesses([...guesses, { input, status }])
        if (input !== updatedCipher[label].decrypted) {
          if (!userHasIncorrectlyGuessedALetter) {
            userHasIncorrectlyGuessedALetter = true
            window.userInteractionEvent('event', 'unlock_achievement', {
              achievement_id: 'make_incorrect_guess',
            })
          }
          debug('This was an incorrect guess', input)
          const newIncorrectGuesses = [
            ...incorrectGuesses,
            { input, status, label },
          ]
          setIncorrectGuesses(newIncorrectGuesses)
          // TODO currently hardmode can never lose, we can show warnings that there are y guesses left if x guesses is realistic for hard mode
          if (
            !isHardMode &&
            newIncorrectGuesses.length > MAX_INCORRECT_GUESSES / 2 &&
            incorrectGuesses.length < MAX_INCORRECT_GUESSES
          ) {
            showErrorAlert(
              WARNING_REMAINING_GUESSES(
                newIncorrectGuesses.length,
                MAX_INCORRECT_GUESSES - newIncorrectGuesses.length
              ),
              {
                persist: false,
                delayMs: REVEAL_TIME_MS,
              }
            )
          }
        }
        debug('updated updatedCipher', updatedCipher)
        setCurrentCipher(updatedCipher)
      }
      if (ariaLabel) {
        setCurrentLetter(ariaLabel)
      }
      //  setCurrentGuess(`${currentGuess}${value}`)

      let areAllLettersGuessed = true
      encryptedLetters.forEach((key) => {
        if (cipher[key] && cipher[key].decrypted !== cipher[key].guesses[0]) {
          areAllLettersGuessed = false
        }
      })
      if (areAllLettersGuessed && !gameWasAlreadyCompleted) {
        window.userInteractionEvent('event', 'level_end', {
          level_name: `Cryptogram ${solutionName}`,
          success: true,
        })
        debug('All the letters have been guessed', guesses)
        tryHardModeTimeout && clearTimeout(tryHardModeTimeout)
        if (isLatestGame && !gameWasAlreadyCompleted) {
          setStats(
            addStatsForCompletedGame(
              solutionName,
              stats,
              incorrectGuesses.length
              // MAX_INCORRECT_GUESSES,
            )
          )
        } else if (!gameWasAlreadyCompleted) {
          // record that they solved an encrypted message
          window.userInteractionEvent('event', 'level_up', {
            level: 'encrypted_message',
            character: GAME_TITLE,
          })
        }
        setIsGameWon(true)
        setCelebrateGameIsWon(true)
        setTimeout(() => {
          setCelebrateGameIsWon(false)
        }, CELEBRATE_TIME_MS)
      }

      // TODO currently hardmode can never lose, we can remove it in the next line if we think x guesses is realistic for hard mode
      if (!isHardMode && incorrectGuesses.length > MAX_INCORRECT_GUESSES - 1) {
        debug(
          'Incorrect guesses are more than the max',
          incorrectGuesses,
          MAX_INCORRECT_GUESSES
        )
        window.userInteractionEvent('event', 'level_end', {
          level_name: `Cryptogram ${solutionName}`,
          success: false,
        })
        if (isLatestGame && !gameWasAlreadyCompleted) {
          setStats(
            addStatsForCompletedGame(
              solutionName,
              stats,
              incorrectGuesses.length + 1
              // MAX_INCORRECT_GUESSES,
            )
          )
        }
        setIsGameLost(true)
      }
    },
    [
      currentLetter,
      currentCipher,
      guesses,
      hintTimeout,
      incorrectGuesses,
      isGameWon,
      isGameLost,
      isHardMode,
      isLatestGame,
      showErrorAlert,
      stats,
      tryHardModeTimeout,
      MAX_INCORRECT_GUESSES,
    ]
  )

  const onDelete = useCallback(() => {
    debug('onDelete', currentLetter, currentCipher[currentLetter].guesses)
    if (
      currentLetter &&
      currentCipher[currentLetter].guesses &&
      currentCipher[currentLetter].guesses.length
    ) {
      const updatedCipher = { ...currentCipher }
      updatedCipher[currentLetter].guesses = []
      updatedCipher[currentLetter].status = undefined
      // could also remove from game guesses
      // setGuesses(guesses.slice(1, guesses.length))
      debug('updated updatedCipher after undo', updatedCipher)
      setCurrentCipher(updatedCipher)
    }
  }, [currentLetter, currentCipher])

  const showHint = () => {
    saveHasUsedHintToLocalStorage()
    if (hintCount >= (isHardMode ? MAX_HINTS : 10)) {
      window.userInteractionEvent('event', 'unlock_achievement', {
        achievement_id: 'exhausted_hints',
      })
      showErrorAlert('No more hints available.', {
        persist: false,
      })
      return
    }
    const undecodedLetters = encryptedLetters.filter(
      (encryptedLetter) =>
        currentCipher[encryptedLetter].guesses[0] !==
        currentCipher[encryptedLetter].decrypted
    )
    console.log('undecodedLetters remaining', undecodedLetters)
    let hint = generateCryptogramHint(
      cipher,
      undecodedLetters,
      solutionIndex + hintCount
    )
    setHintCount(hintCount + 1)
    if (
      hint &&
      hint.encrypted &&
      hint.decrypted
      // solutionLetters.includes(hint.decrypted) &&
      // currentCipher[hint.encrypted].guesses[0] !== hint.decrypted
    ) {
      onChar(hint?.decrypted, hint?.encrypted)
      window.userInteractionEvent('event', 'unlock_achievement', {
        achievement_id: 'use_hint',
      })
    }
    console.log('hintCount', hintCount, hint)
  }

  useEffect(() => {
    const listener = (e: KeyboardEvent) => {
      debug('got an event code', e.code)
      debug('got an event key', e.key)
      if (e.code === 'Backspace') {
        onDelete()
      } else {
        const key = localeAwareUpperCase(e.key)
        // TODO: check this test if the range works with non-english letters
        if (key.length === 1 && key >= 'A' && key <= 'Z') {
          const label = (e?.target as HTMLButtonElement)?.ariaLabel || ''

          onChar(key, label)
        }
        if (isTouchScreen && key && isTouchScreenUserConfused) {
          window.userInteractionEvent('event', 'unlock_achievement', {
            achievement_id: `touch_screen_keyboard_input`,
          })
          setIsTouchScreenUserConfused({
            hasSeenTheModal: isTouchScreenUserConfused.hasSeenTheModal,
            hasSucceededToInput: true,
            placeholderClicks: isTouchScreenUserConfused.placeholderClicks,
          })
        }
      }
    }
    window.addEventListener('keyup', listener)
    return () => {
      window.removeEventListener('keyup', listener)
    }
  }, [onDelete, onChar, isTouchScreenUserConfused])

  const mediaQuery = window.matchMedia('print')
  mediaQuery.addEventListener('change', () => {
    console.log('print mode changed', mediaQuery.matches)
    setIsPrintLayout(mediaQuery.matches)
  })

  useEffect(() => {
    if (isPrintLayout) {
      window.print()
    }
  }, [isPrintLayout])

  useEffect(() => {
    document.title = gameTitle
  }, [gameTitle])

  if (isPrintLayout) {
    const style = {
      fontFamily: 'Courier New',
    }
    return (
      <div className="leading-loose tracking-wide" style={style}>
        <p>
          #{formattedGameDate}: &nbsp;
          {encryptedQuote?.quote?.toLocaleUpperCase()}
          &nbsp;
          {encryptedQuote?.author?.toLocaleUpperCase()}
        </p>
      </div>
    )
  }

  // If this is an Android or iPad user that hasnt already accepted the app works better on desktop,
  // discourage them from opening the game on their device
  if (isAndroid || isIPad) {
    window.userInteractionEvent('event', 'unlock_achievement', {
      achievement_id: 'view_discourage_non_desktop',
    })
    const queryParams = new URLSearchParams(window.location.search)
    queryParams.set(isIPad ? 'ipad' : 'android', 'beta')
    const linkToMobileBeta = `${window.location.origin}${
      window.location.pathname
    }?${queryParams.toString()}`

    return (
      <Div100vh>
        <div className="flex h-full flex-col">
          <Navbar />
          <div className="mx-auto flex h-screen w-3/5 items-center justify-center text-2xl">
            <p className="text-center dark:text-white">
              This app works best on Desktop.
              <br />
              <a
                className="mt-12 inline-block rounded bg-federal-blue-500 px-4 py-2 font-bold text-white hover:bg-federal-blue-700"
                href={linkToMobileBeta}
              >
                I understand, continue anyway!
              </a>
            </p>
          </div>
        </div>
      </Div100vh>
    )
  }

  return (
    <Div100vh>
      <div className="flex h-full flex-col">
        <Navbar
          isHardMode={isHardMode}
          showHint={showHint}
          setIsPrintLayout={setIsPrintLayout}
          setIsHeartModalOpen={setIsHeartModalOpen}
          setIsInfoModalOpen={setIsInfoModalOpen}
          setIsSendMessageModalOpen={setIsSendMessageModalOpen}
          setIsStatsModalOpen={setIsStatsModalOpen}
          setIsDatePickerModalOpen={setIsDatePickerModalOpen}
          setIsSettingsModalOpen={setIsSettingsModalOpen}
        />

        <div className="flex flex-col items-center justify-center">
          <p className="text-base text-gray-600 dark:text-gray-300">
            {formattedGameDate}
          </p>
          {solution.difficultyLevel ? (
            <p className="mt-2 text-base text-gray-600 dark:text-gray-300">
              Level: {solution.difficultyLevel}
            </p>
          ) : null}
          {isHardMode && (
            <p className="text-base text-gray-600 dark:text-gray-300">
              Hard Mode
            </p>
          )}
        </div>

        <div className="mx-auto flex w-full grow flex-col px-1 pb-4 pt-2 sm:px-6 sm:pb-2 sm:pt-2 md:max-w-7xl lg:px-8">
          <div className="mb-16 flex grow flex-col justify-center pb-24 sm:pb-2">
            <Cryptogram
              onChar={onChar}
              cipher={currentCipher}
              isGameWon={celebrateGameIsWon}
              encryptedQuote={encryptedQuote}
              isHardMode={isHardMode}
              isTouchScreenUserConfused={isTouchScreenUserConfused}
              setIsTouchScreenUserConfused={(state) => {
                saveIsTouchScreenUserConfused(state)
                setIsTouchScreenUserConfused(state)
              }}
            />
          </div>
          {celebrateGameIsWon ? null : (
            <Alphabet
              cipher={currentCipher}
              isHardMode={isHardMode}
              isHighContrast={isHighContrastMode}
            />
          )}
          <InfoModal
            isOpen={isInfoModalOpen}
            handleClose={() => {
              setIsInfoModalOpen(false)
              saveHasSeenInfoModalToLocalStorage()
            }}
          />
          <TouchScreenInfoModal
            isOpen={isTouchScreenInfoModalOpen}
            handleClose={() => {
              setIsTouchScreenUserConfused({
                hasSeenTheModal: true,
                hasSucceededToInput:
                  isTouchScreenUserConfused.hasSucceededToInput,
                placeholderClicks: isTouchScreenUserConfused.placeholderClicks,
              })
              saveIsTouchScreenUserConfused(isTouchScreenUserConfused)
              setIsTouchScreenInfoModalOpen(false)
            }}
          />
          <HeartModal
            isOpen={isHeartModalOpen}
            handleClose={() => setIsHeartModalOpen(false)}
          />
          <StatsModal
            isOpen={isStatsModalOpen}
            handleClose={() => setIsStatsModalOpen(false)}
            cipher={cipher}
            encryptedQuote={encryptedQuote}
            gameStats={stats}
            isLatestGame={isLatestGame}
            isGameLost={isGameLost}
            isGameWon={isGameWon}
            handleShareToClipboard={() => showSuccessAlert(GAME_COPIED_MESSAGE)}
            handleShareFailure={() =>
              showErrorAlert(SHARE_FAILURE_TEXT, {
                durationMs: LONG_ALERT_TIME_MS,
              })
            }
            handleMigrateStatsButton={() => {
              window.userInteractionEvent('event', 'unlock_achievement', {
                achievement_id: 'click_transfer_stats',
              })
              setIsStatsModalOpen(false)
              setIsMigrateStatsModalOpen(true)
            }}
            isHardMode={isHardMode}
            isDarkMode={isDarkMode}
            isHighContrastMode={isHighContrastMode}
          />
          <DatePickerModal
            isOpen={isDatePickerModalOpen}
            initialDate={solutionGameDate}
            handleSelectDate={(d) => {
              setIsDatePickerModalOpen(false)
              setGameDate(d)
              setGameTitle(`${GAME_TITLE} - ${formattedGameDate}`)
            }}
            handleClose={() => setIsDatePickerModalOpen(false)}
          />
          <ShareMessageModal
            isOpen={isSendMessageModalOpen}
            handleClose={() => setIsSendMessageModalOpen(false)}
            handleShareFailure={() =>
              showErrorAlert(SHARE_LINK_FAILURE_TEXT, {
                durationMs: LONG_ALERT_TIME_MS,
              })
            }
            handleShareToClipboard={() => showSuccessAlert(LINK_COPIED_MESSAGE)}
          />
          <MigrateStatsModal
            isOpen={isMigrateStatsModalOpen}
            handleClose={() => setIsMigrateStatsModalOpen(false)}
          />
          <SettingsModal
            isOpen={isSettingsModalOpen}
            handleClose={() => setIsSettingsModalOpen(false)}
            isHardMode={isHardMode}
            handleHardMode={handleHardMode}
            isDarkMode={isDarkMode}
            handleDarkMode={handleDarkMode}
            isHighContrastMode={isHighContrastMode}
            handleHighContrastMode={handleHighContrastMode}
          />
          <AlertContainer />
          <link rel="prefetch" href={BOOK_COVER_THUMBNAIL} />
        </div>
      </div>
    </Div100vh>
  )
}

export default App
