import classnames from 'classnames'
import { RefObject, createRef } from 'react'

import { getStoredIsHighContrastMode } from '../../lib/localStorage'
import { CharStatus } from '../../lib/statuses'

const isaLetter = (decryptedValue: string) => {
  return /[a-zA-Z]+/.test(decryptedValue)
}

type Props = {
  encryptedValue: string
  decryptedValue?: string
  status?: CharStatus
  isGameWon?: boolean
  isHardMode?: boolean
  position?: number
  onClick: (input: string, ariaLabel: string) => void
}
const isIphone = /(iPhone|iPad)/.test(navigator.userAgent)
let userHasInteractedWithCell = false
let userHasDroppedALetter = false

export const Cell = ({
  encryptedValue,
  decryptedValue,
  status,
  isGameWon,
  isHardMode,
  onClick,
}: Props) => {
  const isFilled = decryptedValue && !isGameWon
  const isHighContrast = getStoredIsHighContrastMode()
  const isInputBox = isaLetter(encryptedValue)

  const fixedWidth =
    'text-2xl lg:text-2xl xl:text-3xl w-8 h-8 flex items-center justify-center mx-0.5 font-thin rounded dark:text-white focus:ring-4'
  const classes = classnames(fixedWidth)
  const classesPunctuation = classnames(
    'text-2xl lg:text-2xl xl:text-3xl w-1 h-8 flex items-center justify-center mx-0.5 font-thin rounded dark:text-white'
  )

  const classesDecrypted = classnames(fixedWidth, {
    'cell-fill-animation': isFilled,
    // 'animate-glow': isGameWon,
    'animate-glow': isGameWon,
    'border-solid border-2': !isHardMode,
    // 'border-dotted border-2': isHardMode,

    // guess is incorrect
    'hover:bg-federal-blue-100 hover:text-white active:bg-federal-blue-100 active:text-white border-federal-blue-100 dark:border-federal-blue-100':
      !status || status === 'absent',
    'border-federal-blue-900 dark:border-federal-blue-100': isHighContrast,
    absent: status === 'absent',

    // highcontrast mode
    'correct shadowed bg-cyan-500 hover:bg-cyan-600 active:bg-cyan-700 text-white border-cyan-500':
      (isGameWon || status === 'correct') && isHighContrast,
    'present shadowed bg-orange-500 hover:bg-orange-600 active:bg-orange-700 text-white border-orange-500':
      status === 'present' && isHighContrast,

    // guess is correct
    'correct shadowed bg-spring-green-500 hover:bg-spring-green-400 dark:hover:bg-spring-green-300 dark:bg-spring-green-600 active:bg-spring-green-500 text-white border-black dark:border-white':
      (isGameWon || status === 'correct') && !isHighContrast,

    // guess is in the wrong place
    'present shadowed  bg-goldenrod-400 hover:bg-goldenrod-200      dark:bg-goldenrod dark:hover:bg-goldenrod-300 active:bg-goldenrod-300 text-white border-black dark:border-white':
      status === 'present' && !isHighContrast,
  })

  const stylesDecrypted = {
    fontFamily: 'Courier New',
    minheight: '1em',
    minwWidth: '1em',
    borderBottom: isHardMode ? '2px solid' : undefined,
  }

  const stylesPunctuation = {
    fontFamily: 'Courier New',
    minHeight: '1em',
    minWidth: '0.2em',
  }

  const stylesEncrypted = {
    ...stylesDecrypted,
    marginBottom: '0.5em',
    borderBottom: 'none',
  }

  const allowDrop: React.DragEventHandler<HTMLButtonElement> = (event) => {
    event.preventDefault()
  }

  const onDrop: React.DragEventHandler<HTMLButtonElement> = (event) => {
    event.preventDefault()
    var input = event.dataTransfer.getData('text')
    onClick(input, encryptedValue)
    if (!userHasDroppedALetter) {
      userHasDroppedALetter = true
      window.userInteractionEvent('event', 'unlock_achievement', {
        achievement_id: 'drop_on_cryptogam_cell',
      })
    }
  }

  const cellOnClick: React.MouseEventHandler<HTMLButtonElement> = (event) => {
    if (!userHasInteractedWithCell) {
      userHasInteractedWithCell = true
      window.userInteractionEvent('event', 'unlock_achievement', {
        achievement_id: 'click_cryptogram_cell',
      })
    }
    const label = (event?.target as HTMLButtonElement)?.ariaLabel || ''

    onClick('', label)
    if (isIphone) {
      event.currentTarget.blur()
      hiddenInputRef?.current?.focus()
    }
  }

  const notTabbable = isInputBox ? {} : { tabIndex: -1 }
  const hiddenInputRef: RefObject<HTMLInputElement> = createRef()
  return (
    <div className="inline-flex flex-col">
      <button
        aria-label={encryptedValue}
        className={isInputBox ? classesDecrypted : classesPunctuation}
        onClick={cellOnClick}
        onDragOver={allowDrop}
        onDrop={onDrop}
        style={isInputBox ? stylesDecrypted : stylesPunctuation}
        title={`Placeholder for ${encryptedValue}`}
        {...notTabbable}
      >
        {isInputBox ? decryptedValue : null}
        <input
          ref={hiddenInputRef}
          style={{ position: 'absolute', top: '-9999px' }}
          tabIndex={-1}
          type="text"
        />
      </button>

      <div
        aria-label={encryptedValue}
        style={isInputBox ? stylesEncrypted : stylesPunctuation}
        className={isInputBox ? classes : classesPunctuation}
      >
        {encryptedValue}
      </div>
    </div>
  )
}
