import { BaseModal } from './BaseModal'

type Props = {
  isOpen: boolean
  handleClose: () => void
}

const imageUrl = new URL(window.location.href)
imageUrl.pathname = imageUrl.pathname.replace(
  /\/$/,
  '/drag-n-drop-alphabet.gif'
)
const imageSrc = imageUrl.toString()
const imageStyles = {
  height: '198px',
  backgroundColor: 'white',
  marginTop: '1rem',
}

export const TouchScreenInfoModal = ({ isOpen, handleClose }: Props) => {
  if (isOpen) {
    window.userInteractionEvent('event', 'touch_screen_info_begin')
  }

  function onCloseClick() {
    window.userInteractionEvent('event', 'touch_screen_info_complete')
    return handleClose()
  }

  return (
    <BaseModal title="Drag and Drop" isOpen={isOpen} handleClose={onCloseClick}>
      <p className="text-sm text-gray-500 dark:text-gray-300">
        Drag and drop letters from the decryption key at the bottom of the
        screen to the blank spaces in the cryptogram above.
      </p>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        <img
          src={imageSrc}
          alt="Drag and drop letters"
          height="198"
          style={imageStyles}
        />
      </div>
    </BaseModal>
  )
}
