import { Cipher } from '../../lib/cipher'
import { StoreConfusedTouchScreen } from '../../lib/localStorage'
import { Solution } from '../../lib/quotes'
import { Cell } from './Cell'

const isTouchScreen = 'ontouchstart' in window || navigator.maxTouchPoints > 0

type Props = {
  onChar: (input: string, ariaLabel: string) => void
  isGameWon?: boolean
  cipher: Cipher
  encryptedQuote: Solution
  isHardMode?: boolean
  isTouchScreenUserConfused?: StoreConfusedTouchScreen
  setIsTouchScreenUserConfused?: (state: StoreConfusedTouchScreen) => void
}

export const Cryptogram = ({
  onChar,
  cipher,
  encryptedQuote,
  isHardMode = true,
  isTouchScreenUserConfused,
  setIsTouchScreenUserConfused,
  isGameWon,
}: Props) => {
  const words = encryptedQuote?.quote?.toLocaleUpperCase().split(/\s/) ?? []
  const author = encryptedQuote?.author?.toLocaleUpperCase().split(/\s/) ?? []
  // console.log('words are', words)

  const onCharTouchDetectConfusion = (input: string, ariaLabel: string) => {
    // console.log('onCharTouchDetectConfusion', input, isTouchScreenUserConfused)
    let newstate
    if (isTouchScreen && !input && isTouchScreenUserConfused) {
      // console.log('there was no input ', input)

      window.userInteractionEvent('event', 'unlock_achievement', {
        achievement_id: `touch_screen_confused_click_cell`,
      })
      newstate = {
        hasSeenTheModal: isTouchScreenUserConfused.hasSeenTheModal,
        hasSucceededToInput: isTouchScreenUserConfused.hasSucceededToInput,
        placeholderClicks: isTouchScreenUserConfused.placeholderClicks.concat([
          Date.now(),
        ]),
      }
    } else if (isTouchScreen && input && isTouchScreenUserConfused) {
      // console.log('there was an input ', input)
      window.userInteractionEvent('event', 'unlock_achievement', {
        achievement_id: `touch_screen_input`,
      })
      newstate = {
        hasSeenTheModal: isTouchScreenUserConfused.hasSeenTheModal,
        hasSucceededToInput: true,
        placeholderClicks: isTouchScreenUserConfused.placeholderClicks,
      }
    }
    // console.log('here is newstate', newstate)
    if (setIsTouchScreenUserConfused && newstate) {
      setIsTouchScreenUserConfused(newstate)
    }
    onChar(input, ariaLabel)
  }

  function renderLetter(value: string, i: number) {
    if (/\s/.test(value)) {
      return (
        <div key={`${value}-${i}`} test-id="letter">
          {value}
        </div>
      )
    }

    return (
      //<span test-id="letter"> {value}</span>
      <Cell
        onClick={onCharTouchDetectConfusion}
        key={`${value}-${i}`}
        encryptedValue={value}
        decryptedValue={cipher[value] ? cipher[value].guesses[0] : value}
        isHardMode={isHardMode}
        status={cipher[value] && !isHardMode ? cipher[value].status : undefined}
        position={i}
        isGameWon={isGameWon}
      ></Cell>
    )
  }

  function renderWord(word: string, i: number) {
    // console.log('renderword', word)
    return (
      <div
        key={`${word}-${i}`}
        test-id="word"
        className="mr-4 flex whitespace-nowrap lg:mr-8"
      >
        {word.split('').map(renderLetter)}
      </div>
    )
  }

  const stylePhrase = {
    width: '100%',
  }

  return (
    <div
      test-id="phrase"
      className="flex flex-wrap justify-center"
      style={stylePhrase}
      title={
        isTouchScreen
          ? 'Solve this encoded message by dragging and dropping letters of the alphabet to where you want them to go.'
          : 'Solve this encoded message by clicking on the blanks to type your guess.'
      }
    >
      {words.map(renderWord)}
      <div style={{ width: '100%' }} /> {/* This will create a line break */}
      {author.map(renderWord)}
    </div>
  )
}
