import {
  BugAntIcon,
  CalendarIcon,
  ChartBarIcon,
  ChatBubbleOvalLeftIcon,
  CogIcon,
  HeartIcon,
  InformationCircleIcon,
  PrinterIcon,
  PuzzlePieceIcon,
} from '@heroicons/react/24/outline'
import classNames from 'classnames'
import { useState } from 'react'

import { ENABLE_ARCHIVED_GAMES } from '../../constants/settings'
import { GAME_TITLE } from '../../constants/strings'

type Props = {
  isHardMode?: boolean
  setIsDatePickerModalOpen?: (value: boolean) => void
  setIsHeartModalOpen?: (value: boolean) => void
  setIsInfoModalOpen?: (value: boolean) => void
  setIsPrintLayout?: (value: boolean) => void
  setIsSendMessageModalOpen?: (value: boolean) => void
  setIsSettingsModalOpen?: (value: boolean) => void
  setIsStatsModalOpen?: (value: boolean) => void
  showHint?: (value: boolean) => void
}

export const Navbar = ({
  isHardMode,
  setIsDatePickerModalOpen,
  setIsHeartModalOpen,
  setIsInfoModalOpen,
  setIsPrintLayout,
  setIsSendMessageModalOpen,
  setIsSettingsModalOpen,
  setIsStatsModalOpen,
  showHint,
}: Props) => {
  const isMobile = window.innerWidth < 768
  const isTouchDevice = 'ontouchstart' in window || navigator.maxTouchPoints > 0
  const [isHovered, setIsHovered] = useState(isTouchDevice) // always show on Mobile
  const imageUrl = new URL(
    `${window.location.origin}/${window.location.pathname.replace(/\/$/, 'logo192.png')}`
  )

  const classes = classNames(
    `navbar-content short:h-auto  px-2 pb-4 pt-4 text-white lg:pb-8 lg:pt-8`,
    {
      'bg-federal-blue-600': !isHardMode,
      'bg-claret': isHardMode,
    }
  )
  return (
    <div
      className="navbar"
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      <div className={classes}>
        <div className="flex">
          <span
            aria-label="Open Info Modal"
            className="flex flex-col items-center"
            onClick={() => {
              if (typeof setIsInfoModalOpen !== 'function') {
                return
              }
              window.userInteractionEvent('event', 'unlock_achievement', {
                achievement_id: 'open_info_modal',
              })
              setIsInfoModalOpen(true)
            }}
            title="Show how to play"
          >
            <InformationCircleIcon
              className={`h-6 w-6 ${
                setIsInfoModalOpen
                  ? 'cursor-pointer'
                  : 'cursor-not-allowed opacity-50'
              }`}
            />
            <span
              className={`m-1 text-xs ${isHovered ? '' : 'navbar-hidden-text'}`}
            >
              Info
            </span>
          </span>

          <span
            aria-label="Open Archived Games"
            className="flex flex-col items-center"
            onClick={() => {
              if (typeof setIsDatePickerModalOpen !== 'function') {
                return
              }
              window.userInteractionEvent('event', 'unlock_achievement', {
                achievement_id: 'open_archived_games',
              })
              setIsDatePickerModalOpen(true)
            }}
            title="Show archived games"
          >
            {ENABLE_ARCHIVED_GAMES && (
              <CalendarIcon
                className={`h-6 w-6 ${
                  setIsDatePickerModalOpen
                    ? 'cursor-pointer'
                    : 'cursor-not-allowed opacity-50'
                }`}
              />
            )}
            <span
              className={`m-1 text-xs ${isHovered ? '' : 'navbar-hidden-text'}`}
            >
              Date
            </span>
          </span>

          <span
            aria-label="Open Heart Modal"
            className="flex flex-col items-center"
            onClick={() => {
              if (typeof setIsHeartModalOpen !== 'function') {
                return
              }
              window.userInteractionEvent('event', 'unlock_achievement', {
                achievement_id: 'open_heart_modal',
              })
              setIsHeartModalOpen(true)
            }}
            title="Learn how to give love to this game"
          >
            <HeartIcon
              className={`h-6 w-6 ${
                setIsHeartModalOpen
                  ? 'cursor-pointer'
                  : 'cursor-not-allowed opacity-50'
              }`}
            />
            <span
              className={`m-1 text-xs ${isHovered ? '' : 'navbar-hidden-text'}`}
            >
              Support
            </span>
          </span>
        </div>
        <h1
          aria-label={GAME_TITLE}
          className="block truncate text-xl font-bold drop-shadow-md dark:text-white md:hidden"
          style={{ fontFamily: 'courier new' }}
        >
          {' '}
        </h1>
        <h1
          aria-label={GAME_TITLE}
          className="hidden truncate text-xl font-bold drop-shadow-md dark:text-white md:block"
          style={{ fontFamily: 'courier new' }}
        >
          <img
            src={imageUrl.toString()}
            alt="Isabelle"
            className="mr-2 inline"
            height="48"
            width="48"
          />{' '}
          {GAME_TITLE}
        </h1>

        <div className="right-icons">
          {!isMobile ? (
            <span
              aria-label="Print the cryptogram"
              className="flex flex-col items-center"
              onClick={() => {
                if (typeof setIsPrintLayout !== 'function') {
                  return
                }
                setIsPrintLayout(true)
              }}
              title="Click to print the cryptogram"
            >
              <PrinterIcon
                className={`h-6 w-6 ${setIsSendMessageModalOpen ? 'cursor-pointer' : 'cursor-not-allowed opacity-50'}`}
              />
              <span
                className={`m-1 text-xs ${isHovered ? '' : 'navbar-hidden-text'}`}
              >
                Print
              </span>
            </span>
          ) : null}

          <a
            aria-label="Send a bug report"
            href="https://forms.gle/zRP8pAE1JrTEA5bWA"
            target="blank"
            rel="noopener noreferrer"
            className="flex flex-col items-center"
            onClick={() => {
              window.userInteractionEvent('event', 'unlock_achievement', {
                achievement_id: 'click_send_a_bug_report',
              })
            }}
            title="Click to send me a bug report"
          >
            <BugAntIcon className="h-6 w-6 cursor-pointer" />
            <span
              className={`m-1 text-xs ${isHovered ? '' : 'navbar-hidden-text'}`}
            >
              Report
            </span>
          </a>

          <span
            aria-label="Send an encrypted message"
            className="flex flex-col items-center"
            onClick={() => {
              if (typeof setIsSendMessageModalOpen !== 'function') {
                return
              }
              window.userInteractionEvent('event', 'unlock_achievement', {
                achievement_id: 'click_send_a_message',
              })
              setIsSendMessageModalOpen(true)
            }}
            title="Click to write an encrypted message"
          >
            <ChatBubbleOvalLeftIcon
              className={`h-6 w-6 ${setIsSendMessageModalOpen ? 'cursor-pointer' : 'cursor-not-allowed opacity-50'}`}
            />
            <span
              className={`m-1 text-xs ${isHovered ? '' : 'navbar-hidden-text'}`}
            >
              Encrypt
            </span>
          </span>

          <span
            aria-label="Show Hint"
            className="flex flex-col items-center"
            onClick={() => {
              if (typeof showHint !== 'function') {
                return
              }
              window.userInteractionEvent('event', 'unlock_achievement', {
                achievement_id: 'click_show_hint',
              })
              showHint(true)
            }}
            title="Click to insert the hint"
          >
            <PuzzlePieceIcon
              className={`h-6 w-6 ${showHint ? 'cursor-pointer' : 'cursor-not-allowed opacity-50'}`}
            />
            <span
              className={`m-1 text-xs ${isHovered ? '' : 'navbar-hidden-text'}`}
            >
              Hint
            </span>
          </span>

          <span
            aria-label="Open Stats"
            className="flex flex-col items-center"
            onClick={() => {
              if (typeof setIsStatsModalOpen !== 'function') {
                return
              }
              window.userInteractionEvent('event', 'unlock_achievement', {
                achievement_id: 'open_stats',
              })
              setIsStatsModalOpen(true)
            }}
            title="Show stats"
          >
            <ChartBarIcon
              className={`h-6 w-6 ${
                setIsStatsModalOpen
                  ? 'cursor-pointer'
                  : 'cursor-not-allowed opacity-50'
              }`}
            />
            <span
              className={`m-1 text-xs ${isHovered ? '' : 'navbar-hidden-text'}`}
            >
              Stats
            </span>
          </span>

          <span
            aria-label="Open Settings"
            className="flex flex-col items-center"
            onClick={() => {
              if (typeof setIsSettingsModalOpen !== 'function') {
                return
              }
              window.userInteractionEvent('event', 'unlock_achievement', {
                achievement_id: 'open_settings',
              })
              setIsSettingsModalOpen(true)
            }}
            title="Open Settings"
          >
            <CogIcon
              className={`h-6 w-6 ${
                setIsSettingsModalOpen
                  ? 'cursor-pointer'
                  : 'cursor-not-allowed opacity-50'
              }`}
            />
            <span
              className={`m-1 text-xs ${isHovered ? '' : 'navbar-hidden-text'}`}
            >
              Settings
            </span>
          </span>
        </div>
      </div>
    </div>
  )
}
