import { formatISO } from 'date-fns'

import { getToday } from './dateutils'
import { Solution } from './quotes'
import { CharStatus } from './statuses'

const gameStateKey = 'gameState'
const highContrastKey = 'highContrast'

try {
  // Upgrade to the new gameState per day
  if (localStorage.getItem('gameState')) {
    localStorage.clear()
    window.userInteractionEvent('event', 'unlock_achievement', {
      achievement_id: 'localstorage_cleared',
    })
  }
} catch (e) {
  console.error('localStorage is disabled', e)
  window.userInteractionEvent('event', 'unlock_achievement', {
    achievement_id: 'localstorage_disabled',
  })
}

const getGameStateKey = () => {
  const parsed = new URLSearchParams(window.location.search)
  const key =
    parsed.get('code') ||
    parsed.get('d') ||
    formatISO(getToday(), { representation: 'date' })
  return `${gameStateKey}-${key}`
}

export type Guess = {
  input: string
  label?: string
  status?: CharStatus
}

export type StoredGameState = {
  gameWasWon: boolean
  guesses: Guess[]
  incorrectGuesses: Guess[]
  solution: Solution
}

export type StoreConfusedTouchScreen = {
  placeholderClicks: number[]
  hasSeenTheModal: boolean
  hasSucceededToInput: boolean
}

export const saveGameStateToLocalStorage = (gameState: StoredGameState) => {
  const key = getGameStateKey()
  localStorage.setItem(key, JSON.stringify(gameState))
}

export const loadGameStateFromLocalStorage = () => {
  const key = getGameStateKey()
  const state = localStorage.getItem(key)
  return state ? (JSON.parse(state) as StoredGameState) : null
}

export const loadHasSeenInfoModalFromLocalStorage = () => {
  const state = localStorage.getItem('hasSeenInfoModal')
  return state ? true : false
}

export const saveHasSeenInfoModalToLocalStorage = () => {
  localStorage.setItem('hasSeenInfoModal', new Date().toUTCString())
}

export const loadHasUsedHintFromLocalStorage = () => {
  const state = localStorage.getItem('hasUsedHint')
  return state ? true : false
}

export const saveHasUsedHintToLocalStorage = () => {
  localStorage.setItem('hasUsedHint', new Date().toUTCString())
}

export const loadHasUsedHardModeFromLocalStorage = () => {
  const state = localStorage.getItem('hasUsedHardMode')
  return state ? true : false
}

export const saveHasUsedHardModeToLocalStorage = () => {
  localStorage.setItem('hasUsedHardMode', new Date().toUTCString())
}

export const loadIsTouchScreenUserConfused = () => {
  const state = localStorage.getItem('isTouchScreenUserConfused')
  return state
    ? (JSON.parse(state) as StoreConfusedTouchScreen)
    : {
        placeholderClicks: [],
        hasSeenTheModal: false,
        hasSucceededToInput: false,
      }
}
export const saveIsTouchScreenUserConfused = (
  state: StoreConfusedTouchScreen
) => {
  localStorage.setItem('isTouchScreenUserConfused', JSON.stringify(state))
}

const gameStatKey = 'gameStats'

export type GameStats = {
  winDistribution: number[]
  gamesFailed: number
  currentStreak: number
  bestStreak: number
  totalGames: number
  successRate: number
}

export const saveStatsToLocalStorage = (gameStats: GameStats) => {
  localStorage.setItem(gameStatKey, JSON.stringify(gameStats))
}

declare global {
  interface Window {
    gtag: any
    userInteractionEvent: any
  }
}

export const loadStatsFromLocalStorage = () => {
  const stats = localStorage.getItem(gameStatKey)
  return stats ? (JSON.parse(stats) as GameStats) : null
}

export const setStoredIsHighContrastMode = (isHighContrast: boolean) => {
  if (isHighContrast) {
    localStorage.setItem(highContrastKey, '1')
  } else {
    localStorage.removeItem(highContrastKey)
  }
}

export const getStoredIsHighContrastMode = () => {
  const highContrast = localStorage.getItem(highContrastKey)
  return highContrast === '1'
}
