import { UAParser } from 'ua-parser-js'

import { GAME_TITLE } from '../constants/strings'
import { Cipher } from './cipher'
import { Guess } from './localStorage'
import {
  Solution,
  getGameDateForQuery,
  message,
  solutionGameDate,
  solutionIndex,
} from './quotes'

const webShareApiDeviceTypes: string[] = ['mobile', 'smarttv', 'wearable']
const parser = new UAParser()
const browser = parser.getBrowser()
const device = parser.getDevice()

export const shareToClipboard = ({
  itemId,
  handleShareFailure,
  handleShareToClipboard,
  textToShare,
}: {
  itemId?: string
  handleShareFailure: (error: Error) => void
  handleShareToClipboard: () => void
  textToShare: string
}) => {
  const shareData = { text: textToShare }

  let shareSuccess = false

  try {
    if (attemptShare(shareData)) {
      navigator.share(shareData)
      shareSuccess = true
      window.userInteractionEvent('event', 'share', {
        method: 'Navigator Share API',
        content_type: 'text',
        item_id: itemId,
      })
    }
  } catch (error) {
    shareSuccess = false
  }

  try {
    if (!shareSuccess) {
      if (navigator.clipboard) {
        navigator.clipboard
          .writeText(textToShare)
          .then(() => {
            window.userInteractionEvent('event', 'share', {
              method: 'Navigator Clipboard API',
              content_type: 'text',
              item_id: itemId,
            })
            handleShareToClipboard()
          })
          .catch((err) => {
            // TODO capture errors in gtag
            handleShareFailure(err)
          })
      } else {
        handleShareFailure(new Error('Clipboard API not available'))
      }
    }
  } catch (error) {
    handleShareFailure(error as Error)
  }
}

type shareProps = {
  cipher: Cipher
  encryptedQuote: Solution
  handleShareFailure: () => void
  handleShareToClipboard: () => void
  isDarkMode: boolean
  isGameWon: boolean
  isHighContrastMode: boolean
}

export const shareStatus = ({
  cipher,
  encryptedQuote,
  handleShareFailure,
  handleShareToClipboard,
  isDarkMode,
  isGameWon,
  isHighContrastMode,
}: shareProps) => {
  const date = getGameDateForQuery(solutionGameDate)

  const queryParams = new URLSearchParams(window.location.search)
  queryParams.set('utm_source', 'beta_app')
  queryParams.set('utm_medium', 'share_button')
  queryParams.set('utm_campaign', 'share')
  // Remove any existing device query params so the next user can acknowledge if they are using the app on an unsupported device
  queryParams.delete('ipad')
  queryParams.delete('android')
  if (!message) {
    queryParams.set('d', date)
  }
  const link = `${window.location.origin}${
    window.location.pathname
  }?${queryParams.toString()}`

  const words = encryptedQuote?.quote?.toLocaleUpperCase().split(/\s/) ?? []
  const author = encryptedQuote?.author?.toLocaleUpperCase().split(/\s/) ?? []
  const toBeShared = words
    .concat(author)
    .map((word) => {
      return word
        .concat(' ')
        .split('')
        .map((char) => {
          return {
            input: char || ' ',
            status: cipher[char]?.firstIncorrectGuess
              ? cipher[char]?.firstIncorrectGuess
              : cipher[char]?.status,
          }
        })
    })
    .flat()

  const textToShare =
    `I ${isGameWon ? 'solved' : 'tried to solve'} ${message ? message : GAME_TITLE} ${date} \n` +
    generateEmojiGrid(
      toBeShared,
      getEmojiTiles(isDarkMode, isHighContrastMode)
    ) +
    `\nCan you solve it?\n` +
    `${link}`

  shareToClipboard({
    handleShareFailure,
    textToShare,
    handleShareToClipboard,
    itemId: `${message} ${solutionIndex}`,
  })
}

export const generateEmojiGrid = (guesses: Guess[], tiles: string[]) => {
  return (
    // guesses.map(({ input }) => input).join('') +
    // '\n' +
    guesses
      .map(({ status }) => {
        if (status === 'correct') {
          return tiles[0]
        } else if (status === 'present') {
          return tiles[1]
        } else if (status === 'absent') {
          return tiles[2]
        }
        return ' '
      })
      .join('')
      .trim()
  )
}

const attemptShare = (shareData: object) => {
  return (
    // Deliberately exclude Firefox Mobile, because its Web Share API isn't working correctly
    browser.name?.toUpperCase().indexOf('FIREFOX') === -1 &&
    webShareApiDeviceTypes.indexOf(device.type ?? '') !== -1 &&
    navigator.canShare &&
    navigator.canShare(shareData) &&
    navigator.share
  )
}

export const getEmojiTiles = (
  isDarkMode: boolean,
  isHighContrastMode: boolean
) => {
  let tiles: string[] = []
  tiles.push(isHighContrastMode ? '' : '')
  tiles.push(isHighContrastMode ? '' : '')
  tiles.push(isDarkMode ? '' : '')
  return tiles
}
